.Tabs {
  color: lightseagreen; }

.Tabs__controls {
  min-width: 200px;
  white-space: nowrap; }

.Tabs__control {
  padding: 4px 20px;
  background: none;
  border: 1px solid #333;
  outline: none;
  font: inherit;
  color: #333;
  cursor: pointer; }
  .Tabs__control:first-child {
    border-radius: 4px 0 0 4px; }
  .Tabs__control:last-child {
    border-radius: 0 4px 4px 0; }
  .Tabs__control--active {
    background: #333;
    -webkit-box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5) inset;
            box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5) inset;
    color: #FFF;
    pointer-events: none; }

.Tabs__control--active .Tabs__control-text {
  border: none; }

.Tabs__item {
  display: none;
  padding-top: 24px; }
  .Tabs__item--active {
    display: block; }



